<template>
  <tr class="cart-product" v-if="shoppingCartObj">
    <td>
      <img class="cart__product-img" :src="shoppingCartObj.product.images[0]" alt="product-image">
    </td>
    <td class="cart__product-info">
      <h3 class="cart__product-name">
        <router-link :to="`/product/${shoppingCartObj.product.id}`" tag="a">
          {{ shoppingCartObj.product.name }}
        </router-link>
      </h3>
      <p class="cart__product-id">#{{ shoppingCartObj.product.id }}</p>
    </td>
    <td class="cart__product-quantity">
      <div class="quantity">
        <div class="quantity__minus" @click="changeQuantity">-</div>
        <input class="quantity__input" type="number" disabled :value="shoppingCartObj.quantity">
        <div class="quantity__plus" @click="changeQuantity">+</div>
      </div>
    </td>
    <td class="cart__product-price">
      ${{ (shoppingCartObj.product.price * shoppingCartObj.quantity).toFixed(2) }}
    </td>
    <td>
      <a href="#" class="cart__product-remove" @click.prevent="removeProduct">
        <img src="img/icons/dismiss-icon.svg" alt="remove-product-icon">
      </a>
    </td>
  </tr>
</template>

<script>

export default {
  props: ['shoppingCartObj'],
  methods: {
    removeProduct() {
      this.$store.dispatch('remove', this.shoppingCartObj);
    },
    changeQuantity(e) {
      if (e.target.innerHTML === '+') {
        if (this.shoppingCartObj.quantity >= 1 && this.shoppingCartObj.quantity < 10) {
          this.$store.dispatch('update', { product: this.shoppingCartObj.product, quantity: this.shoppingCartObj.quantity+1 });
        }
      } else if (e.target.innerHTML === '-') {
        if (this.shoppingCartObj.quantity > 1 && this.shoppingCartObj.quantity <= 10) {
          this.$store.dispatch('update', { product: this.shoppingCartObj.product, quantity: this.shoppingCartObj.quantity-1 });
        }
      }
    }
  }
}
</script>