<template>
  <div>
    <!-- Header -->
    <app-header headerClass="header--white"></app-header>

    <!-- Cart -->
    <div class="cart">
      <div class="container">
        <!-- Heading -->
        <div class="cart__heading">
          <h1>Shopping Cart</h1>
          <img src="img/icons/checkout-icons-1.svg" alt="checkout-icons" class="checkout__icons">
        </div>
        <!-- Products Table -->
        <table class="cart__table display" v-if="shoppingCart.length !== 0">
          <thead>
            <tr class="card__table-heading">
              <th>Product</th>
              <th>&nbsp;</th>
              <th>Ammount</th>
              <th>Price</th>
              <th>&nbsp;</th>
            </tr>
          </thead>
          <tbody>
            <app-cart-product v-for="obj in shoppingCart" :key="obj.product.id" :shoppingCartObj="obj"></app-cart-product>
          </tbody>
        </table>
        <!-- Cart No Products -->
        <p class="cart__no-products" v-else>No products in the cart</p>
        <!-- Cart Footer -->
        <div class="cart__footer">
          <router-link to="/shop" tag="a" class="cart__continue">
            <img src="img/icons/arrow-black.svg" alt="black-arrow"> Continue Shopping
          </router-link>
          <div class="cart__cost">
            Total Cost: <span class="price">${{ shoppingCartTotal }}</span>
          </div>
          <router-link to="/checkout" tag="a" class="btn btn--orange" :class="{disabled: shoppingCart.length === 0}">Checkout</router-link>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import Header from '../components/Header.vue';
import CartProduct from '../components/CartProduct.vue';

export default {
  components: {
    'app-header': Header,
    'app-cart-product': CartProduct
  },
  data() {
    return {
    };
  },
  computed: {
    shoppingCart() {
      return this.$store.getters.shoppingCart;
    },
    shoppingCartTotal() {
      return this.$store.getters.shoppingCartTotal;
    }
  }
}
</script>